import React, { Component } from 'react';

class gt extends Component {
 
    render() { 
        return (
            <div className="gt">
            <h3>Chào các bạn, mình là Tiendatmagic, mình có thể đọc được suy nghĩ của bạn</h3>
            <h4>Cách chơi:</h4>
            <h4>Bạn hãy suy nghĩ trong đầu một con số nằm trong khoảng từ 1 đến 100, hãy
                nghĩ trong đầu. Và khi bắt đầu trò chơi, bạn chỉ cần chọn Có với những bảng có
                con số bạn đang nghĩ, ngược lại nếu không có con số bạn đang nghĩ thì chọn Không</h4>
            <h4>Nếu bạn đã sẵn sàng, bấm Start để bắt đầu chơi</h4>
            <input
                type="button"
                defaultValue="START"
                className="btn btn-success btn-lg btn-start pt-3 pb-3 pt-3 pr-3"/>
        </div>
          );
    }
}
 
export default gt;