import React, {Component} from 'react';
class Resulterror extends Component {

    render() {
        return (
            <div
                className="result-error"
                style={{
                display: 'none'
            }}>
                <h2>Xin lỗi con số bạn đang nghĩ không hợp lệ, vui lòng chọn lại, ahihi {'🤣'}</h2>
                <input
                    type="button"
                    defaultValue="Bấm vào đây để chọn lại"
                    className="btn btn-success btn-reload pt-3 pb-3 pt-3 pr-3"/>
            </div>

        );
    }
}

export default Resulterror;