import React, {Component} from 'react';
class Result extends Component {

    render() {
        return (
            <div className="result" style={{
                display: 'none'
            }}>
                <h2>Con số bạn đang nghĩ là:</h2>
                <div
                    name="result-magic"
                    id="result-magic"
                    className="display-1"
                    style={{
                    color: '#ff0099'
                }}/>
                <br/>
                <h3>Ngạc nhiên chưa {'😂😂😂'}</h3>
                <br/>
                <input
                    type="button"
                    defaultValue="Bấm vào đây để chọn lại"
                    className="btn btn-success btn-reload pt-3 pb-3 pt-3 pr-3"/>
            </div>
        );
    }
}

export default Result;